<template>
  <div class="explore">
      <header class="header-bg">
          
      </header>
    <div id="domId1"></div>
<bottom mode="top"/>
    
    <div id="domId2"></div>
<bottom mode="mid"/>

    <div id="domId3"></div>
<bottom mode="bottom"/>


  </div>
</template>
<script>
import lottie from "lottie-web";
import bottom from './bottom'
export default {
   components: {
    bottom,
  },
  data() {
    return {
      keyword: "",
      info: {
        close_time: 0,
        cut_off_total: "",
        ledger_version: "",
        nbp_amount: 0,
        service_charge: 0,
      },
      time: new Date().toLocaleString("en-US", { hour12: false }),
    };
  },
  created() {
    // this.querySearch("nb0bb1a30393894f0fedebc8bf9513f51");
  },
  mounted() {
    this.initSvg();
  },
  methods: {
    initSvg() {
      lottie.loadAnimation({
        container: document.getElementById("domId1"), // 绑定dom节点
        renderer: "svg", // 渲染方式:svg、canvas
        loop: true, // 是否循环播放，默认：false
        autoplay: true, // 是否自动播放, 默认true
        animationData: require("../assets/json/data1.json"), // AE动画使用bodymovie导出为json数据
      });
          lottie.loadAnimation({
        container: document.getElementById("domId2"), // 绑定dom节点
        renderer: "svg", // 渲染方式:svg、canvas
        loop: true, // 是否循环播放，默认：false
        autoplay: true, // 是否自动播放, 默认true
        animationData: require("../assets/json/data2.json"), // AE动画使用bodymovie导出为json数据
      });
          lottie.loadAnimation({
        container: document.getElementById("domId3"), // 绑定dom节点
        renderer: "svg", // 渲染方式:svg、canvas
        loop: true, // 是否循环播放，默认：false
        autoplay: true, // 是否自动播放, 默认true
        animationData: require("../assets/json/data3.json"), // AE动画使用bodymovie导出为json数据
      });
    },
  },
};
</script>
<style lang="less">
#domId {
  margin-top: 30px;
}
.explore {
  background:#00007B;
  padding-bottom:90px;
.header-bg{
    background: url("data:image/png;base64,") no-repeat center;
    width:100vw;
    background-size:100% 100%;
    height: calc(100vw * (890 / 1080))
}

svg{
  display: block;

}
}
</style>
