<template>
  <div class="about">
    <div class="search-wrap">
      <input
        type="text"
        v-model="keyword"
        class="search-input"
        v-on:keyup.enter="searchHander"
        placeholder="Input Wallet Address"
      />
      <img
        @click="searchHander"
        class="search-icon"
        src="data:image/png;base64,"
        alt=""
      />
    </div>
    <div class="total-wrap">
      <div class="total-bg">
        <div class="about-title">NBP Amount:</div>
        <div class="about-number">{{ info.nbp_amount || "-" }}</div>
      </div>
      <div class="total-bg">
        <div class="about-title">Service Charge:</div>
        <div class="about-number">{{ info.service_charge || "-" }}</div>
      </div>
    </div>
    <div id="domId"></div>
    <div class="about-bottom">
      <img class="about-bottom-img" src="data:image/png;base64," alt="" />
      <div class="about-bottom-table">
        <div class="about-bottom-table-row">
          <div class="about-label">Ledger Version:</div>
          <div class="about-value">{{ info.ledger_version || "-" }}</div>
        </div>
        <div class="about-bottom-table-row">
          <div class="about-label">Cut-Off Total：</div>
          <div class="about-value">
            {{
              (info.cut_off_total !== "" && info.cut_off_total + "  NBP") || "-"
            }}
          </div>
        </div>
        <div class="about-bottom-table-row">
          <div class="about-label">CloseTime(UTC)</div>
          <div class="about-value">
            {{
              (info.close_time &&
                formatTime(new Date(info.close_time * 1000))) ||
              "-"
            }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import request from "./../api/request";
import lottie from "lottie-web";
function pad(number) {
  if (number < 10) {
    return "0" + number;
  }
  return number;
}
export default {
  data() {
    return {
      keyword: "",
      info: {
        close_time: 0,
        cut_off_total: "",
        ledger_version: "",
        nbp_amount: 0,
        service_charge: 0,
      },
      time: new Date().toLocaleString("en-US", { hour12: false }),
    };
  },
  created() {
    // this.querySearch("nb0bb1a30393894f0fedebc8bf9513f51");
  },
  mounted() {
    this.initSvg();
  },
  methods: {
    searchHander() {
      this.querySearch(this.keyword);
    },
    initSvg() {
      lottie.loadAnimation({
        container: document.getElementById("domId"), // 绑定dom节点
        renderer: "svg", // 渲染方式:svg、canvas
        loop: true, // 是否循环播放，默认：false
        autoplay: true, // 是否自动播放, 默认true
        animationData: require("../assets/json/data.json"), // AE动画使用bodymovie导出为json数据
      });
    },
    querySearch(params) {
      request({
        url: "/explore/explorer",
        method: "post",
        params: {
          address: params,
        },
      }).then((res) => {
        this.info = res.data;
        this.time = res.time;
      });
    },
    formatTime: (date) => {
      return (
        date.getUTCFullYear() +
        "-" +
        pad(date.getUTCMonth() + 1) +
        "-" +
        pad(date.getUTCDate()) +
        " " +
        pad(date.getUTCHours()) +
        ":" +
        pad(date.getUTCMinutes()) +
        ":" +
        pad(date.getUTCSeconds())
      );
    },
  },
};
</script>
